import {thirdPartyAuthDelete, thirdPartyAuthList} from "@/api/userInfo";
import moment from '@/utils/moment';
import rfLoadMore from '@/components/rf-load-more/rf-load-more';
import empty from "@/components/empty";
export default {
	components: {
		rfLoadMore,
		empty
	},
	data() {
		return {
			timeOutEvent: 0,
			source: 0,
			page: 1,
			loadingType: 'more',
			thirdPartyAuthList: [],
			loading: true
		}
	},
	filters: {
		time(val) {
			return moment(val * 1000).format('YYYY-MM-DD HH:mm:ss')
		},
		oauthClientFilter(val) {
			switch (val) {
				case 'wechat':
					return '微信';
					break;
				case 'wechatMp':
					return '微信小程序';
					break;
				case 'qq':
					return 'QQ小程序';
					break;
				case 'sina':
					return '新浪';
					break;
				default:
					break;
			}
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.page = 1;
		this.thirdPartyAuthList = [];
		this.loading = true;
		this.getThirdPartyAuthList('refresh');
	},
	//加载更多
	onReachBottom(){
		this.page ++;
		this.getThirdPartyAuthList();
	},
	onLoad(option){
		this.source = option.source;
	},
	onShow() {
		this.initData()
	},
	methods: {
		/**
		 *@des 初始化数据
		 *@author stav stavyan@qq.com
		 *@blog https://stavtop.club
		 *@date 2019/11/18 09:57:30
		 */
		initData () {
			this.getThirdPartyAuthList();
		},
		/**
		 *@des 获取收货地址列表
		 *@author stav stavyan@qq.com
		 *@blog https://stavtop.club
		 *@date 2019/11/18 09:58:15
		 */
		async getThirdPartyAuthList (type) {
			await this.$get(`${thirdPartyAuthList}`).then(r=>{
				this.loading = false;
				if (type === 'refresh') {
					uni.stopPullDownRefresh();
				}
				this.loadingType  = r.data.length === 10 ? 'more' : 'nomore';
				this.thirdPartyAuthList = [ ...this.thirdPartyAuthList, ...r.data ];
			}).catch(() => {
				this.loading = false;
				if (type === 'refresh') {
					uni.stopPullDownRefresh();
				}
			})
		},
		/**
		 *@des 解绑第三方授权登录
		 *@author stav stavyan@qq.com
		 *@blog https://stavtop.club
		 *@date 2019/12/31 16:47:33
		 */
		async unBind (id) {
			
			await this.$del(`${thirdPartyAuthDelete}?id=${id}`).then(r=>{
					this.page = 1;
					this.thirdPartyAuthList = [];
					this.getThirdPartyAuthList();
			}).catch(err => {
				console.log(err)
			})
		},
	}
}
